
#header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #191b1d;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: #e6e7e9;
  width: 100%;
  height: 7vh;
  padding: 20px;
  z-index: 99999;
  top: 0;
  right: 0;
  -webkit-box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 20px 2px rgba(0, 0, 0, 0.5);
}
#header a {
    color: #e6e7e9;
}
#header i.icon {
    margin-right: 10px;
}
#header .social-icons,
  #header .travel-info,
  #header .statistics {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
#header .social-icons a,
    #header .travel-info a,
    #header .statistics a {
      -webkit-transition: opacity .3s;
      transition: opacity .3s;
}
#header .social-icons a:hover,
      #header .travel-info a:hover,
      #header .statistics a:hover {
        opacity: .6;
}
#header .social-icons i.icon {
    margin-right: 15px;
}
#header .travel-info {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
#header .travel-info .current, #header .travel-info .next {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
#header .travel-info .current i, #header .travel-info .next i {
        color: #fff;
}
#header .next {
    margin-left: 10px;
}
#header .next i.icon.ion-plane {
      -webkit-transform: rotate(90deg);
      transform: rotate(90deg);
}
#header .statistics {
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
}
#header .statistics p {
      -webkit-transition: opacity .3s;
      transition: opacity .3s;
      cursor: pointer;
}
#header .statistics p:hover {
        opacity: .6;
}



